	import gotoLogin from '@/pages/components/login'
	import benefit from '@/pages/detail/benefit'
	import  category from '@/pages/detail/category'
	import  floor  from '@/pages/detail/floor'
	import my from '@/pages/detail/my'
	import uniSearch from '@/components/lee-search/lee-search'
	import {mapState} from 'vuex'
	export default {
		components: {
			benefit,
			category,
			floor,
			uniSearch,
			my,
			gotoLogin
		},
		data() {
			return {
				tabBars: [{
				    name: '商户优惠',
				    id: 'youhui'
				}, {
				    name: '商家分类',
				    id: 'fenlei'
				}, {
				    name: '楼层商家',
				    id: 'shangjia'
				}],
				inputAttr : {
					backgroundColor: '#ffffff',
					placeholderText: ' '
				},
				tabIndex: 0,
				iconSrc : {
					logo: '/static/lee-search/icon_search.png'
				},
				avatar:'/static/user.png',
				showLogin :"Image",
			}	
		},
		mounted(){
			
		},
		onShow() {
			if(this.ismember) {
				this.showLogin = ""

			}if(this.tempLogin){
				this.showLogin = ""
			}
		},
		computed:{
			...mapState(['tempLogin','ismember'])
		},
		methods: {
			ontabtap(e) {
			    let index = e.target.dataset.current || e.currentTarget.dataset.current
			     this.tabIndex = index
			     this.scrollInto = this.tabBars[index].id
		},
		hiddendiag(){
			// uni.redirectTo({
			// 	url: '/pages/index/index'
			// })
			this.showLogin = ""
		},
		gtLogin(){
			this.showLogin = 'Image'
		}
		
	},
}
